import buttonEvents from "../data/button-events";
import { closeSideNav } from "../page/pageInit";


const initEvents = () => {
    window.addEventListener('resize', (e) => {
        closeSideNav();
    });
    buttonEvents.forEach((item, i) => {
        const element = document.querySelector(`#${item.name}`);
        element.addEventListener(item.type, item.action);
    });
}

export default initEvents;