export const containClass = (element, className) => {
    const id = typeof element == 'string' ? document.querySelector(element) : element;
    return id.classList.contains(className);
}

export const addClass = (element, className) => {
    const id = typeof element == 'string' ? document.querySelector(element) : element;
    if (!containClass(element, className)) {
        id.classList.add(className);   
    }
    return;
}

export const removeClass = (element, className) => {
    const id = typeof element == 'string' ? document.querySelector(element) : element;
    if (containClass(element, className)) {
        id.classList.remove(className);   
    }
    return;
}

export const addClasses = (identifier, className) => {
    const elementArr = document.querySelectorAll(identifier);
    const elements = Object.values(elementArr);
    elements.forEach((element, i) => {
        addClass(element, className);
    });
}

export const removeClasses = (identifier, className) => {
    const elementArr = document.querySelectorAll(identifier);
    const elements = Object.values(elementArr);
    elements.forEach((element, i) => {
        removeClass(element, className);
    });
}