import { pageData } from "../data/pageData";
import { addClass, containClass, removeClass } from "../helpers/classHelpers";

const header = document.querySelector('header');

export const closeSideNav = () => {
    const pageWidth = window.innerWidth;
    if (pageWidth <= pageData.small) {
        return;
    }
    const sidenav = document.querySelector('.links-container');
    if(containClass(sidenav, 'opened')){
        removeClass(sidenav, 'opened');
        addClass(sidenav, 'closed');
    }
}

const pageInit = () => {
    
}

export default pageInit;