import { runAnimation } from "../helpers/animationHelpers";
import { addClass, containClass, removeClass } from "../helpers/classHelpers";

export const buttonEvents = [
    {
        name: 'sidebar-toggler',
        type: 'click',
        action: (e) => {
            const sidebar = document.querySelector('.links-container');
            if (containClass(sidebar, 'closed')) {
                removeClass(sidebar, 'closed');
            }
            runAnimation(sidebar, ['opening', 'opened'], 300);
        }
    },
    {
        name: 'overlay',
        type: 'click',
        action: (e) => {
            const sidebar = document.querySelector('.links-container');
            removeClass(sidebar, 'opened');
            runAnimation(sidebar, ['closing', 'closed'], 300);
        }
    },
    {
        name: 'features',
        type: 'click',
        action: (e) => {
            const element = e.path[1];
            console.log(element, element.ariaExpanded);
            if (element.ariaExpanded == 'false') {
                removeClass(element, 'collapse');
                addClass(element , 'collapsed');
                element.ariaExpanded = true;
                return;
            }

            removeClass(element, 'collapsed');
            addClass(element , 'collapse');
            element.ariaExpanded = false;
        }
    },
    {
        name: 'company',
        type: 'click',
        action: (e) => {
            const element = e.path[1];
            console.log(element, element.ariaExpanded);
            if (element.ariaExpanded == 'false') {
                removeClass(element, 'collapse');
                addClass(element , 'collapsed');
                element.ariaExpanded = true;
                return;
            }

            removeClass(element, 'collapsed');
            addClass(element , 'collapse');
            element.ariaExpanded = false;
        }
    },
    {
        name: 'close-btn',
        type: 'click',
        action: (e) => {
            const sidebar = document.querySelector('.links-container');
            removeClass(sidebar, 'opened');
            runAnimation(sidebar, ['closing', 'closed'], 300);
            // setTimeout(() => {
            //     if (containClass(sidebar, 'closing')) {
            //         removeClass(sidebar, 'closing');
            //         addClass(sidebar, 'closed');
            //     }
            // }, 350);
        }
    }
]

export default buttonEvents;