import { addClass, containClass, removeClass } from "./classHelpers";


export const runAnimation = (element, classlist, time) => {
    if (typeof classlist != "object") {
        return;
    }
    const elem = element;
    const classes = classlist;
    if (!containClass(elem,classes[0])) {
        addClass(elem, classes[0]);   
    }
    const timeInterval = classlist.length !== 2 ? time / classlist.length : time;
    let iterator = 0;

    let intervalId = setInterval(() => {
        iterator++;
        if (classes.length > iterator) {
            classlist.forEach((item, i) => {
                if (containClass(elem, item)) {
                    removeClass(elem, item);
                }
            });
            addClass(elem, classlist[iterator]);
        }
        else{
            clearInterval(this);
        }
    }, timeInterval);
}